/* Styl nawigacji */
nav ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

nav ul li {
  margin-right: 20px;
}

nav ul li:last-child {
  margin-right: 0;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  background-color: #0077b6; /* Zmieniony kolor tła przycisków */
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

nav ul li a:hover {
  background-color: #005f80; /* Zmiana koloru tła przy hover */
  transform: scale(1.05); /* Efekt powiększenia przy hover */
}

nav ul li a:active {
  background-color: #00465e; /* Zmiana koloru tła przy kliknięciu */
}

/* Ogólny styl strony */
.App {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4; /* Tło dla całej aplikacji */
  color: #333; /* Kolor tekstu */
  min-height: 100vh; /* Wysokość strony na pełny ekran */
}

.App-header {
  background-color: #282c34;
  color: white;
  padding: 20px;
  text-align: center;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

/* Styl dla komponentów */
section {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  margin: 20px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

section h2 {
  color: #0077b6;
}

/* Styl dla przycisków */
button {
  background-color: #0077b6; /* Kolor tła przycisku */
  color: white; /* Kolor tekstu */
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:hover {
  background-color: #005f80; /* Zmiana koloru tła przy hover */
  transform: scale(1.05); /* Efekt powiększenia przycisku */
}

button:active {
  background-color: #00465e; /* Kolor tła przycisku po kliknięciu */
}

/* Styl dla kart użytkowników */
.user-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 16px;
  width: 200px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.user-card img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-bottom: 16px;
}

.user-card h2 {
  margin: 10px 0;
  font-size: 1.2rem;
}

.user-card p {
  margin: 5px 0;
  font-size: 1rem;
}

/* Styl dla inputa w Zadaniu 2 */
input {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

input:focus {
  border-color: #0077b6;
}

/* Styl dla głównej strony */
main {
  padding: 20px;
}

section {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.App-header h1 {
  font-size: 2rem;
  color: #fff;
}

h1, h2, h3 {
  font-family: 'Arial', sans-serif;
}
